import Vue from 'vue'
import VueRouter from 'vue-router'
import * as path from "path";
// import Login from "../views/create/Login";

const Home = () => import('../views/home/Home');
const About = () => import('../views/about/About');
const Community = () => import('../views/community/Community');
const Shop = () => import('../views/shop/Shop');
const Store = () => import('../views/store/Store');
const Login = () => import('../views/create/Login');
const Create = () => import('../views/create/Create');
const Person = () => import('../views/person/Person');
const Storetwo = () => import('@v/store/Storetwo');
const Storethree = () => import('@v/store/Storethree');
Vue.use(VueRouter);

const routes = [
  {
    path: '',
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/about',
    component: About
  },
  {
    path: '/community',
    component: Community
  },
  {
    path: '/shop',
    component: Shop
  },
  {
    path: '/store',
    component: Store,
    // children: [
    //   {
    //     path: 'storetwo',
    //     component: Storetwo
    //   }
    // ]
  },
  {
    path: '/storetwo',
    component: Storetwo
  },
  {
    path: '/storethree',
    component: Storethree
  },
  {
    path: '/person',
    component: Person
  },
  {
    path: '/login',
    component: Login,
    // children: [
    //   {
    //     path: '/create',
    //     name: 'create',
    //     component: Create,
    //   }
    // ]
  },
];

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router
